body {
  background-color: #cefcf5;
}
.container {
  width: 607px;
  height: 750px;
  margin: auto;
  margin-top: 50px;
  border-radius: 12px;
  background-image: linear-gradient(180deg, #130754 0%, #3b2f80 100%);
  overflow: hidden;
  box-shadow: 0 8px 32px 0 #3b2f80;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.top-bar {
  display: flex;
  justify-content: center;
  gap: 14px;
  padding-top: 50px;
}

.top-bar input {
  display: flex;
  width: 362px;
  height: 60px;
  border-radius: 40px;
  outline:1px solid #ebfffc;
  box-shadow: 0 8px 32px 0 #3b2f80;
  border: 1px solid rgba(255, 255, 255, 0.18);
  background-color: transparent;
  padding-left: 40px;
  color: #ebfffc;
  font-size: 20px;
  font-weight: 400;
}
::placeholder{
    color: #b8c7c5;
 
}
.search-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  gap: 78px;
  background: transparent;
  outline: 1px solid #ebfffc;
  box-shadow: 0 5px 10px 0 #3b2f80;
  border-radius: 40px;
  cursor: pointer;
}

.weather-image {
  margin-top: 29px;
  display: flex;
  justify-content: center;
  
}
.weather-image img {
  height: 250px;
  width: 250px;
  filter: drop-shadow(0 0  0.20rem white);
  
  
}
.weather-temp {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 100px;
  font-weight: 400;
}
.weather-location {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 40px;
  font-weight: 400;
}

.data-container {
  display: flex;
  margin-top: 50px;
  justify-content: center;
  color: white;
}
.element {
  margin: auto;
  display: flex;
  gap: 12px;
  align-items: flex-start;
}
.data {
  font-size: 30px;
  font-weight: 400;
}
.text {
  font-size: 18px;
  font-weight: 400;
}
.icon {
  margin-top: 10px;
}
@media only screen and (max-width: 600px) {
  .container {
    width: 92%;
    height: auto;
    margin-top:12vh;
    border-radius: 10px;
  
  }

  .top-bar {
    flex-direction: column;
    padding-top: 20px;
    margin-left: 2px;
  }

  .top-bar input {
    width: 86%;
    margin-bottom: 10px;
    font-size: 14px;
    font-weight: 400;
  
  }

  .search-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    padding: 10px;
    border-radius: 50%;
    width: 25px;
   
  }

  .weather-image img {
    height: 150px;
    width: 150px;
    filter: drop-shadow(0 0  0.20rem white);

  }

  .weather-temp {
    font-size: 50px;
  }

  .weather-location {
    font-size: 20px;
  }

  .data-container {
    flex-direction: row;
    margin-top: 20px;
  }

  .element {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .data {
    font-size: 16px;
  }

  .text {
    font-size: 15px;
    margin-bottom: 20px;
  }
}
